<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Rune Attribute </h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="Name" >
        <el-input v-model="model.name"> </el-input>
      </el-form-item>
  
      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<style>

</style>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{},
      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/runeattributes/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/runeattributes',this.model).then
      }
      this.$router.push('/runeattribute/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/runeattributes/${this.id}`)
    this.model = res.data
    },
  }, 
  created(){

    this.id && this.fetch()
  }
  }
</script>
