import Vue from 'vue'
import Router from 'vue-router'
import Main from '../views/Main.vue'
import newCategory from '../views/newCategory.vue'
import categoryList from '../views/categoryList.vue'
import newEquipment from '../views/newEquipment.vue'
import equipmentList from '../views/equipmentList.vue'
import newHero from '../views/newHero.vue'
import heroList from '../views/heroList.vue'
import newArticle from '../views/newArticle.vue'
import articleList from '../views/articleList.vue'
import newAdvertisement from '../views/newAdvertisement.vue'
import advertisementList from '../views/advertisementList.vue'
import newAdminUser from '../views/newAdminUser.vue'
import adminUserList from '../views/adminUserList.vue'
import newSummonerSpell from '../views/newSummonerSpell.vue'
import summonerSpellsList from '../views/summonerSpellsList.vue'
import runeAttributesList from '../views/runeAttributesList.vue'
import newRuneAttribute from '../views/newRuneAttribute.vue'
import runesList from '../views/runesList.vue'

import newRune from '../views/newRune.vue'

import login from '../views/login.vue'

Vue.use(Router)

const router = new Router({
routes:[
  {
    path:'/login',
    name:'login',
    component:login,
    meta:{ isPublic:true },
  },
  {
    path: '/',
    name: 'Main',
    component: Main,
    children:[
      {
        path:'/category/new',
        component:newCategory
      },
      {
        path:'/category/list',
        component:categoryList
      },
      {
        path:'/category/edit/:id',
        component:newCategory,
        props:true
      },
      {
        path:'/equipment/new',
        component:newEquipment
      },
      {
        path:'/equipment/list',
        component:equipmentList
      },
      {
        path:'/equipment/edit/:id',
        component:newEquipment,
        props:true
      },
      {
        path:'/summonerspell/new',
        component:newSummonerSpell
      },
      {
        path:'/summonerspell/list',
        component:summonerSpellsList
      },
      {
        path:'/summonerspell/edit/:id',
        component:newSummonerSpell,
        props:true
      },
      {
        path:'/runeattribute/new',
        component:newRuneAttribute
      },
      {
        path:'/runeattribute/list',
        component:runeAttributesList
      },
      {
        path:'/runeattribute/edit/:id',
        component:newRuneAttribute,
        props:true
      },
      {
        path:'/rune/new',
        component:newRune
      },
      {
        path:'/rune/list',
        component:runesList
      },
      {
        path:'/rune/edit/:id',
        component:newRune,
        props:true
      },
      {
        path:'/hero/new',
        component:newHero
      },
      {
        path:'/hero/list',
        component:heroList
      },
      {
        path:'/hero/edit/:id',
        component:newHero,
        props:true
      },
      {
        path:'/article/new',
        component:newArticle
      },
      {
        path:'/article/list',
        component:articleList
      },
      {
        path:'/article/edit/:id',
        component:newArticle,
        props:true
      },
      {
        path:'/advertisement/new',
        component:newAdvertisement
      },
      {
        path:'/advertisement/list',
        component:advertisementList
      },
      {
        path:'/advertisement/edit/:id',
        component:newAdvertisement,
        props:true
      },
      {
        path:'/admin-user/new',
        component:newAdminUser
      },
      {
        path:'/admin-user/list',
        component:adminUserList
      },
      {
        path:'/admin-user/edit/:id',
        component:newAdminUser,
        props:true
      },
    ]
  },
 
]
})

router.beforeEach((to,from, next)=>{
  console.log(`Is public route: ${to.meta.isPublic}`);
  console.log(`User token: ${localStorage.token}`);

  if(!to.meta.isPublic && !localStorage.token){
    return next('/login')
  }
  next()
})
export default router
