<template>
    <el-row type="flex" style="flex-wrap: wrap;">
      <el-col :md="12" v-for="(item, i) in partners" :key="i">
        <el-form-item label="Hero Name">
          <el-select filterable v-model="item.hero">
            <el-option
              v-for="partnerHero in heroes"
              :key="partnerHero._id"
              :value="partnerHero._id"
              :label="partnerHero.name"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="Discription">
          <el-input type="textarea" v-model="item.discription" />
        </el-form-item>
        
        <el-form-item>
          <el-button size="small" type="danger" @click="removeHero(i)">
            Delete
          </el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </template>
  
  <script>
  export default {
    props: {
      partners: {
        type: Array,
        required: true
      },
      heroes: {
        type: Array,
        required: true
      },
    
    },
    methods: {
      removeHero(index) {
        this.partners.splice(index, 1);
      }
    }
  };
  </script>