<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Equipments </h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="Name" >
        <el-input v-model="model.name"> </el-input>
      </el-form-item>
      <el-form-item label="Icon">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{},
      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/equipments/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/equipments',this.model).then
      }
      this.$router.push('/equipment/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/equipments/${this.id}`)
    this.model = res.data
    },
    async afterUpload(res){
      this.$set(this.model,'icon',res.url)
    }
  }, 
  created(){

    this.id && this.fetch()
  }
  }
</script>
