<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Articles </h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="Category">
        <el-select v-model="model.multipleCategories" multiple > 
          <el-option 
          v-for="item in multipleCategories"
          :key="item._id"
          :label="item.name"
          :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Title" >
        <el-input v-model="model.title"> </el-input>
      </el-form-item>
      <el-form-item label="Details" >
        <vue-editor v-model="model.body" useCustomImageHandler @image-added="handleImageAdded">
          
        </vue-editor>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { VueEditor} from "vue2-editor";

  export default{
    components:{
      VueEditor
    },
    props:{
    id : {}
  },
    data(){
      return{
        model:{},
        multipleCategories:[],
      }
    },
  methods: {
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {

      const formData = new FormData();
      formData.append("file", file);
      const res = await this.$http.post('upload',formData)
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/articles/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/articles',this.model).then
      }
      this.$router.push('/article/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/articles/${this.id}`)
    this.model = res.data
    },
    async fetchMultipleCategories(){
    const res = await this.$http.get('rest/categories')
    this.multipleCategories = res.data
    },
  }, 
  created(){
    this.fetchMultipleCategories()
    this.id && this.fetch()
  }
  }
</script>
