<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Runes</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="Name" >
        <el-input v-model="model.name"> </el-input>
      </el-form-item>
      <el-form-item label="Icon">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
        <div v-for="(effect, index) in model.effect" :key="index">
          <el-form-item label="Effect Name">
            <el-select v-model="effect.name" placeholder="Select effect name">
              <el-option v-for="item in runeAttributes" :key="item._id" :value="item._id" :label="item.name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="Boots Score">
            <el-input v-model="effect.bootsScore" placeholder="Enter boots score"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" size="mini" @click="model.effect.splice(index, 1);">Remove Effect</el-button>
          </el-form-item>
      </div>
      <el-form-item label="">
      <el-button size="small" @click="model.effect.push({});"><i class="el-icon-plus"></i> Add Effect</el-button></el-form-item>
        

      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<style>

</style>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{
          effect:[]
        },
        runeAttributes:[],
        
      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/runes/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/runes',this.model).then
      }
      this.$router.push('/rune/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/runes/${this.id}`)
    this.model = res.data
    },
    async fetchAttributes(){
    const res = await this.$http.get('rest/runeattributes')
    this.runeAttributes = res.data
    },
    async afterUpload(res){
      this.$set(this.model,'icon',res.url)
    }
  }, 
  created(){

    this.id && this.fetch()
    this.fetchAttributes()
  }
  }
</script>
