<template>
  <div>
   <h1>Category List</h1>
   <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240"></el-table-column>
            <el-table-column prop="ParentCategory.name" label="Parent Category"></el-table-column>
            <el-table-column prop="name" label="Category Name"></el-table-column>
           
            <el-table-column fixed="right" label="Operations" width="180">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="$router.push(`/category/edit/${scope.row._id}`)">Edit</el-button>
                  <el-button type="text" size="small" @click="deleteCategory(scope.row)">Delete</el-button>
                </template>
            </el-table-column>
    </el-table> 
  </div>
</template>
<script>
export default{
  
  data() {
  return {
    items:[],
  }
},
methods:{
  async fetch(){
    const res = await this.$http.get('rest/categories')
    this.items =res.data
  },
  async deleteCategory(row){
        this.$confirm(`This "${row.name}" will permanently deleted?`, 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/categories/${row._id}`).then
          this.fetch()
          this.$message({
            type: 'success',
            message: 'Delete completed'
          })
        }).catch(() => {
            this.$message({
              type: 'info',
              message: 'Delete canceled'
            }); 
          })
}

},
created(){
  this.fetch()
}
 
}

</script>
