<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Category </h1>
    <el-form @submit.native.prevent="save">
      <el-form-item label="Parent Category">
        <el-select v-model="model.ParentCategory"> 
          <el-option 
          v-for="item in ParentCategory"
          :key="item._id"
          :label="item.name"
          :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Name">
        <el-input v-model="model.name"> </el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{},
        ParentCategory:[]
      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/categories/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/categories',this.model).then
      }
      this.$router.push('/category/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/categories/${this.id}`)
    this.model = res.data
    },
    async fetchParent(){
    const res = await this.$http.get('rest/categories')
    this.ParentCategory = res.data
    }

  },
  
  created(){
    this.fetchParent()
    this.id && this.fetch()
  }
  }
</script>
