<template>
  <div>
   <h1>Heroes List</h1>
   <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240"></el-table-column>
            <el-table-column prop="avatar" label="Avatar">
              <template slot-scope="scope">
                <img :src="scope.row.avatar" style="height: 3rem;">
              </template>
            </el-table-column>
            <el-table-column prop="name" label="Hero Name"></el-table-column>
            <el-table-column prop="title" label="Hero Title"></el-table-column>
            <el-table-column prop="Category[0].name" label="Hero Category"></el-table-column>
            <el-table-column fixed="right" label="Operations" width="180">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="$router.push(`/hero/edit/${scope.row._id}`)">Edit</el-button>
                  <el-button type="text" size="small" @click="deleteHero(scope.row)">Delete</el-button>
                </template>
            </el-table-column>
    </el-table> 
  </div>
</template>
<script>
export default{
  
  data() {
  return {
    items:[],


  }
},
methods:{
  async fetch(){
    const res = await this.$http.get('rest/heroes')
    this.items =res.data
  },
  // async fetchHeroCategory(){
  //   const heroCategory = await this.$http.get('rest/categories')
  //   // this.items =res.data
  // },
  async deleteHero(row){
        this.$confirm(`This "${row.name}" will permanently deleted?`, 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/heroes/${row._id}`).then
          this.$message({
            type: 'success',
            message: 'Delete completed'
          })
          this.fetch();
        }).catch(() => {
            this.$message({
              type: 'info',
              message: 'Delete canceled'
            }); 
          })
}

},
created(){
  this.fetch()
  // this. fetchHeroCategory()
}
 
}

</script>
