<template>
  <div>
   <h1>Rune List</h1>
   <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240"></el-table-column>
            <el-table-column prop="icon" label="icon">
              <template slot-scope="scope">
                <img :src="scope.row.icon" style="height: 3rem;">
              </template>
            </el-table-column>
            <el-table-column prop="name" label="Rune Name"></el-table-column>
            <el-table-column fixed="right" label="Operations" width="180">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="$router.push(`/rune/edit/${scope.row._id}`)">Edit</el-button>
                  <el-button type="text" size="small" @click="deleteEquipment(scope.row)">Delete</el-button>
                </template>
            </el-table-column>
    </el-table> 
  </div>
</template>
<script>
export default{
  
  data() {
  return {
    items:[],
  }
},
methods:{
  async fetch(){
    const res = await this.$http.get('rest/runes')
    this.items =res.data
  },
  async deleteEquipment(row){
        this.$confirm(`This "${row.name}" will permanently deleted?`, 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/runes/${row._id}`).then
          this.$message({
            type: 'success',
            message: 'Delete completed'
          })
          this.fetch();
        }).catch(() => {
            this.$message({
              type: 'info',
              message: 'Delete canceled'
            }); 
          })
}

},
created(){
  this.fetch()
}
 
}
</script>
