<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Admin User </h1>
    <el-form @submit.native.prevent="save">
     
      <el-form-item label="User Name">
        <el-input v-model="model.username"> </el-input>
      </el-form-item>
      <el-form-item label="Password">
        <el-input type="text" v-model="model.password"> </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{},
      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/adminusers/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/adminusers',this.model).then
      }
      this.$router.push('/admin-user/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/adminusers/${this.id}`)
    this.model = res.data
    },
    
  },
  
  created(){

    this.id && this.fetch()
  }
  }
</script>
