<script>
  export default{
    props:{
    id : {},
  },
    data(){
      return{
        tabLabel: '',
        model:{
          name :'',
          avatar :'',
          scores:{
            difficult:0,
            skill:0,
            attack:0,
            survive:0
          },
          skills:[],
          theBestPartners:[],
          counteredByWhom:[],
          countered:[]
          ,
          summonerSpells:[],
          recommendedRunes:[]
        },
        Category:[],
        equipments:[],
        heroes:[],
        summonerSpells:[],
        recommendedRunes:[]

      }
    },
    
  methods: {
    async fetchSummonerSpells(){
      const res = await this.$http.get(`rest/summonerspells`)
      this.summonerSpells = res.data
    },
    async fetchrecommendedRunes(){
      const res = await this.$http.get(`rest/runes`)
      this.recommendedRunes = res.data
    },
    async fetchCategory(){
      const res = await this.$http.get(`rest/categories`)
      this.Category = res.data
    },
    async fetchEquipments(){
      const res = await this.$http.get(`rest/equipments`)
      this.equipments = res.data
    },
    async fetchHeroes(){
    const res = await this.$http.get(`rest/heroes`)
    this.heroes = res.data
    },
    async afterUpload(res){
      // this.$set(this.model,'avatar',res.url)
      this.model.avatar = res.url
    },
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/heroes/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/heroes',this.model).then
      }
      this.$router.push('/hero/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async handleTabClick(tab) {
    // 根据 tab 的 label 设置不同的 tabLabel 值
    if (tab.label === "The Best Partners") {
      this.tabLabel = "The Best Partners";
    } else if (tab.label === "Countered by whom") {
      this.tabLabel = "Countered by whom";
    } else if (tab.label === "Countered") {
      this.tabLabel = "Countered";
    }

    // 更新每个 partner 的 relationship 字段
    this.model.partners = this.model.partners.map(partner => ({
      ...partner,
      relationship: this.tabLabel
    }));
  },

    async fetch(){
    const res = await this.$http.get(`rest/heroes/${this.id}`)
    this.model = Object.assign({},this.model,res.data)
    },


  },
   
  
  created(){
    this.fetchCategory()
    this.fetchEquipments()
    this.id && this.fetch()
    this.fetchHeroes()
    this.fetchSummonerSpells()
    this.fetchrecommendedRunes()
  }
  }
</script>
