<template>
  <div class="about">
    <h1>{{ id ? 'Edit' : 'New'}} Advertisement </h1>
    <el-form @submit.native.prevent="save" label-width="140px">
      <el-form-item label="Name">
        <el-input v-model="model.name"> </el-input>
      </el-form-item>
      <el-form-item label="Ads">
        <el-button size="small" @click="model.items.push({})"><i class="el-icon-plus"></i> Add Ads</el-button>
          <el-row type="flex" style="flex-wrap: wrap;">
            <el-col :md="24" v-for="(item,i) in model.items" :key="i">
              <el-form-item label="redirect link(url)" style="margin-top: 2rem;">
                <el-input v-model="item.url"></el-input>
              </el-form-item>
              <el-form-item label="Image" style="margin-top: 2rem;">
                <el-upload
                  class="avatar-uploader"
                  :action="uploadUrl"
                  :headers="getAuthHeaders()"
                  :show-file-list="false"
                  :on-success="res => $set(item,'img', res.url)">
                  <img v-if="item.img" :src="item.img" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
            <el-form-item>
              <el-button size="small" type="danger" @click="model.items.splice(i,1)">Delete</el-button>
            </el-form-item>
            </el-col>
          </el-row>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" native-type="submit">Save</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default{
    props:{
    id : {}
  },
    data(){
      return{
        model:{
          items:[{}]
        },

      }
    }
    ,
  methods: {
    async save(){
      
      if(this.id){
          await this.$http.put(`rest/advertisements/${this.id}`,this.model).then
      }
      else{
         await this.$http.post('rest/advertisements',this.model).then
      }
      this.$router.push('/advertisement/list')
      this.$message({
        type:'success',
        message:'Successfully Save!'
      })
    },
    async fetch(){
    const res = await this.$http.get(`rest/advertisements/${this.id}`)
    this.model = Object.assign({},this.model,res.data)
    },

  },
  
  created(){
    this.id && this.fetch()
  }
  }
</script>
